<template>
  <div class="template-container" v-loading="loading">
    <div class="template-container-title">创建人群-广告受众</div>

    <el-form ref="form" :model="form">
      <!--选择广告推广计划 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">1.选择广告推广计划</div>
        <div class="temp-item-main">
            <el-transfer
                filterable
                :filter-method="filterMethod"
                filter-placeholder="推广计划名称"
                v-model="form.campaign_id_list"
                :data="campaignList"
                :titles="['未选', '已选']"
                panelWidth="500px;">
            </el-transfer>
        </div>
      </div>
      <!--选择广告推广计划 end -->
      <!--选择广告行为 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">2.选择广告行为</div>
        <div class="temp-item-main">
            <div>
                <el-radio-group v-model="form.rule_type" @change="ruleTypeChange" class="temp-radio-group">
                    <span class="temp-radio-item" :key="key" v-for="(item, key) in rule_type_list" style="padding-right:20px;">
                        <el-radio-button :label="key" >{{item}}</el-radio-button>
                    </span>
                </el-radio-group>
                <div class="conversion-box" v-show='conversionShow'>
                    <el-checkbox-group v-model="form.conversion_type">
                        <el-checkbox class="conversion-checkbox" :key="key" :label="key" v-for="(val, key) in conversion_type_list" >{{ val }}</el-checkbox>
                    </el-checkbox-group>
                </div>

            </div>
        </div>
      </div>
      <!--选择广告行为 end -->
      <!--选择时间范围 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">3.选择时间范围</div>
        <div class="temp-item-main">

          <div class="temp-item-block">
            <div class="temp-item-inlineblock">
                最近
              <input class="temp-long-inputtext" v-model="form.time_window" type="text" style="margin-left:8px;width:20px;border: 1px solid #e3e4e5;" />
              天内
                <el-tooltip placement="bottom" effect="light">
                <div slot="content">
                    <p>数据将实时更新，计算距离当天往前最近{{form.time_window}}天内</p>
                    <p>的数据</p>
                </div>
                <el-button type="text"><i class="el-icon-question"></i></el-button>
                </el-tooltip>
            </div>
          </div>
            
          <div class="temp-item-block">
            <label class="temp-item-label" for="">人群名称：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-long-inputtext" v-model="form.name" @input="audienceNameChange" type="text" maxlength="32" />
              <span class="temp-item-inlayremark">{{ name_size }}/32</span>
            </div>
          </div>
          <div class="temp-item-block">
            <label class="temp-item-label" for="">人群描述：</label>
            <div class="temp-item-inlineblock">
                <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="（选填）" v-model="form.description"></el-input>
            </div>
          </div>
        </div>
      </div>
      <!--选择时间范围 end -->
      <el-form-item>
        <el-button type="primary" @click="onSubmit">完成</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  created: function() {
    this.getEnumList()
    if (this.account_id) {
        this.getAdCampaigns()
    }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
        this.getAdCampaigns()
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      loading: false,
      conversionShow: false,
      name_size : 0,
      description_size : 0,
      form: {
        type: 'AD',
        campaign_id_list: [],
        rule_type: '',
        conversion_type: [],
        time_window: 30,
        name: '',
        description: '',
      },
      rule_type_list: {},
      conversion_type_list: {},
      campaignList: [],
    }
  },
  methods: {
    onCancel() {
      this.$router.push('/setting/audience')
    },
    audienceNameChange(e) {
      this.name_size = this.form.name.length
    },
    onSubmit() {
        
      if(this.form.campaign_id_list.length == 0){
          this.$message.error('请选择推广计划')
          return false
      }
      if(this.form.rule_type == ''){
          this.$message.error('请选择广告行为')
          return false
      }
      if(this.form.rule_type == 'CONVERSION' && this.form.conversion_type.length == 0){
          this.$message.error('请选择转化类型')
          return false
      }
      if(this.form.time_window > 60){
          this.$message.error('时间范围最大值为60')
          return false
      }
      if(this.form.name == ''){
          this.$message.error('人群名称不能为空')
          return false
      }
      var params = this.form
      var that = this
      that.loading = true
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_CustomAudiences/add', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/setting/audience')
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    getEnumList() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.rule_type_list = respData.data['RULE_TYPE']
            that.conversion_type_list = respData.data['CONVERSION_TYPE']
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    //获取推广计划
    getAdCampaigns: function () {
        let params = { page: 1, page_size: 100 }
        var that = this
        params['account_id'] = this.account_id
        this.$axios
        .get('/Admin/Marketing_Campaigns/get', {
        params: params
        })
        .then(function (response) {
        var respData = response.data
        if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
            that.formatCampaignList(respData.data.list)
            //that.page_info = respData.data.page_info
            }
        } else {
            that.$message(respData.msg)
        }
        })
        .catch(function (err) {
        that.$message('网络繁忙，请稍后再试！')
        that.loadingCampaign = false
        })
    },

    formatCampaignList(list) {
        this.campaignList = []
        list.forEach((val, index) => {
          this.campaignList.push({
            label: val.campaign_name,
            key: val.campaign_id
          });
        });
    },
      
    filterMethod(query, item) {
        return item.label.indexOf(query) > -1;
    },
    ruleTypeChange(val) {
        if(val == 'CONVERSION'){
            this.conversionShow = true
        }else{
            this.conversionShow = false
        }
    }

  },
}
</script>
<style>
.temp-item-main .el-transfer {
    width: 1600px;
}
.temp-item-main .el-transfer-panel {
    width: 550px;
}
.conversion-box {
    border: 1px solid #e3e4e5;
    max-width: 200px;
    position: relative;
    zoom: 1;
    margin-top: 10px;
    padding-bottom: 10px;
}
.conversion-checkbox {
    margin-left: 30px;
    padding-top: 10px;
}
</style>



